#searchTopicsInput {
    border-radius: 0;
    padding-top: 25px;
    padding-bottom: 25px;
    font-size: 1.5em;
    border-left: none;
}

#searchIcon {
    border-right: none;
    background-color: #fff;
    color: #888;
}

#searchFontElement {
    font-size: 1.7em;
    padding-top: 4px;
}

.accordion > .card:first-of-type {
    border-bottom: 1px solid rgba(0,0,0,.125);
}

.topicButtons > .badge {
    border-radius: 0;
    padding-top: 5px;
    padding-bottom: 5px;
    margin-top: 15px;
    margin-right: 20px;
    cursor: pointer;
}

.card-header .badge {
    border-radius: 0;
    padding-top: 5px;
    padding-bottom: 5px;
}

.card-header .badge:not(:first-child) {
    margin-left: 10px;
}

.topicButtons {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
}

.introWrapper {
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: center;
    align-items: center;
}

.title {
    width: 60%;
    text-align: center;
}

.intro {
    width: 60%;
    text-align: center;
}

.card {
    border-radius: 0;
}

.card-header {
    cursor: pointer;
}

@media only screen and (max-width: 900px) {
    .card-header .badge {
        display: none;
    }

    .title {
        width: 100%;
    }

    .intro {
        width: 100%;
    }
} 

@media only screen and (max-width: 500px) {
    #searchTopicsInput {
        font-size: 1.2em;
    }
}

@media only screen and (max-width: 400px) {
    #searchTopicsInput {
        font-size: 1em;
    }
}